import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';


@Injectable()
export class LessonProvider {

  constructor(public http: HttpClient) {
    console.log('Hello LessonProvider Provider');
  }

  createLesson(creator, email, title, description, lesson, contributions, contributors, isPrivate, date):Observable<any> {
    return this.http.post('https://eventureapp.herokuapp.com/api/lesson/create', {
      creator,
      email,
      title,
      description,
      lesson,
      contributions,
      contributors,
      isPrivate,
      date
    });
  }

  searchLessons(lesson): Observable<any> {
    return this.http.post('https://eventureapp.herokuapp.com/api/lesson/search-lesson',{
      lesson: lesson
    });
  }

  contributeLesson(creator, email, title, contributor, contribution, date=Date.apply(Date.now())): Observable<any> {
    return this.http.post('https://eventureapp.herokuapp.com/api/lesson/contribute', {
      creator,
      email,
      title,
      contributor,
      contribution,
      date
    });
  }

}



