//login page
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HelloIonicPage } from '../hello-ionic/hello-ionic';
import { SignUpPage } from '../sign-up/sign-up';
import { RegisterProvider } from '../../providers/register/register';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  //email and password variables grabbed from the html 
  email:string;
  password:string;
  fullname:string;
  //loading circle thingy -- uses loading controller
  loading:any;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private reg: RegisterProvider,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private storage: Storage) { }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
    this.storage.clear();
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Authenticating...',
    });
    this.loading.present();
  }

  submitLogin() {
    if (this.email === undefined || this.password === undefined) {
      let alert = this.alertCtrl.create({
        title: 'Login Error',
        subTitle: 'One or more fields were empty',
        buttons: ['OK']
      });
      alert.present();  
    } else {
      this.showLoading();
      this.reg.loginUser(this.email, this.password)
        .subscribe(res => {
          this.loading.dismiss();
          console.log(res);
          if (res.user) {
            let userData = {
              name: res.user.fullname,
              email: res.user.email
            };
            this.storage.clear();
            this.storage.set('userEmail', userData.email);
            this.storage.set('userName', userData.name);
            this.navCtrl.setRoot(HelloIonicPage, {data: userData});
          }
          if (res.error) {
            let alert = this.alertCtrl.create({
              title: 'Login Error',
              subTitle: res.error,
              buttons: ['OK']
            });
            alert.present();
          }
        });
        //resets the inputs to blank
        this.email = '';
        this.password = '';
    }
  }
  
  //placeholder for page navigation before login works or gets changes
  mockLogin() {
    this.navCtrl.setRoot(HelloIonicPage);
  }

  //push SignUpPage onto the stack
  registerPage() {
    this.navCtrl.push(SignUpPage);
  }

}



