import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';
import { Storage } from '@ionic/storage';
import { HelloIonicPage } from '../hello-ionic/hello-ionic';




class Event {
  eventOwner:string;
  description:string;
  endDate:Date;
  guests:Array<string>;
  items:Array<string>;
  rankingType:number;
}

@IonicPage()
@Component({
  selector: 'page-event',
  templateUrl: 'event.html',
})
export class EventPage {
  //input for eventName etc ...
  eventCreator:string;
  creatorEmail:string;
  eventName:string;
  endDate:Date;
  //input for items
  inputAddItem:string;
  itemList:Array<string> = [];
  //rank type
  ordinalSelected:boolean;
  highMediumLowSelected:boolean;
  description:string; 
  //input for guests
  inputAddGuest:string;
  guestList:Array<string> = [];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private eventProvider: EventProvider,
    private storage: Storage,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController) { 
      this.storage.get('userEmail').then(email => {
        this.creatorEmail = email;
        console.log(`debug: ${this.creatorEmail}`);
      });
      this.storage.get('userName').then(name =>{
        this.eventCreator = name;
        console.log(`debug ${this.eventCreator}`);

      });
    }

  addItem() {
    this.itemList.push(this.inputAddItem);
    this.itemList.forEach(item => {console.log(`${item}`)})
    this.inputAddItem = '';
  }

  remove(item) {
    let i = this.itemList.indexOf(item);
    if (i != -1) {
      this.itemList.splice(i,1);
    }
    this.itemList.forEach(word => {console.log(word)});
  }

  addGuest() {
    this.guestList.push(this.inputAddGuest);
    this.guestList.forEach(guest => {console.log(`${guest}`)})
    this.inputAddGuest = '';
  }

  removeGuest(guest) {
    let i = this.guestList.indexOf(guest);
    if (i != -1) {
      this.guestList.splice(i,1);
    }
    this.guestList.forEach(word => {console.log(word)});
  }

  selectOrdinal() {
    this.ordinalSelected = true;
    this.highMediumLowSelected = false;
  }

  selectHighMediumLow() {
    this.highMediumLowSelected = true;
    this.ordinalSelected = false;
  }

  clear() {
    this.eventCreator = '';
    this.creatorEmail = '';
    this.eventName = '';
    this.endDate = null;
    this.inputAddItem = '';
    this.itemList = [];
    this.ordinalSelected = null;
    this.highMediumLowSelected = null;
    this.description = ''; 
    this.inputAddGuest = '';
    this.guestList = [];
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad EventPage');
  }


  createEvent() {
    this.eventProvider.createEvent(this.eventCreator, this.creatorEmail, 
      this.eventName, this.endDate, this.itemList, this.ordinalSelected, 
      this.highMediumLowSelected, this.description, this.guestList)
        .subscribe(res => {
          if (res.message) {
            let toast = this.toastCtrl.create({
              message: res.message,
              duration: 3000,
              position: 'top'
            });
            toast.present();
            this.navCtrl.pop();
          }
          if (res.error) {
            let alert = this.alertCtrl.create({
              title: 'Event Creation Error',
              subTitle: res.error,
              buttons: ['OK']
            });
            alert.present();
          }
          console.log(res);
        });
        this.clear();
  }

}


