import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { LoginPage } from '../login/login';

//providers
import { RegisterProvider } from '../../providers/register/register';
import { HelloIonicPage } from '../hello-ionic/hello-ionic';

import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-sign-up',
  templateUrl: 'sign-up.html',
})
export class SignUpPage {
  fullname:string;
  email:string;
  password:string;

  loading:any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private reg: RegisterProvider,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private storage: Storage) { }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignUpPage');
  }

  userSignup() {
    if (this.fullname === undefined || this.email === undefined || this.password === undefined) {
      let alert = this.alertCtrl.create({
        title: 'Sign Up Error',
        subTitle: 'One or more fields were empty',
        buttons: ['OK']
      });
      alert.present();  
    } else {
      this.showLoading();
      this.reg.registerUser(this.fullname, this.email, this.password)
        .subscribe(res => {
          this.loading.dismiss();
          console.log(res);
          if (res.user) {
            let userData = {
              name: res.user.fullname,
              email: res.user.email
            };
            this.storage.set('userEmail', userData.email);
            this.storage.set('userName', userData.name);
            this.navCtrl.setRoot(HelloIonicPage, {data: userData});
          }
          if (res.error) {
            let alert = this.alertCtrl.create({
              title: 'Sign Up Error',
              subTitle: res.error,
              buttons: ['OK']
            });
            alert.present();
          }
        });
  
        this.fullname = '';
        this.email = '';
        this.password = '';
    }
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Authenticating...',
      duration: 3000
    });
    this.loading.present();
  }

  loginPage() {
    this.navCtrl.setRoot(LoginPage);
  }

}
 


