import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { LessonProvider } from '../../providers/lesson/lesson';
import { HelloIonicPage } from '../hello-ionic/hello-ionic';


@IonicPage()
@Component({
  selector: 'page-contribute-lesson',
  templateUrl: 'contribute-lesson.html',
})
export class ContributeLessonPage {
  contributor:any;
  contributorEmail:any;
  contribution:string;
  date:Date = Date.apply(Date.now());
  creator:string;
  creatorEmail:any;
  title:string;

  lesson:any;

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private storage: Storage,
    private lessonProvider: LessonProvider,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController) {
      this.lesson = this.navParams.get('lesson');
      this.title = this.lesson.title;
      this.creator = this.lesson.creator;
      this.creatorEmail = this.lesson.email;
    }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ContributeLessonPage');
  }

  goBack() {
    this.navCtrl.pop();
  }

  async getName() {
    await this.storage.get('userName').then(value => {
      this.contributor = value;
    });
    await this.storage.get('userEmail').then(value => {
      this.contributorEmail = value;
    })
  }

  async confirm() {
    await this.getName();
    console.log(`debug: ${this.creator}, ${this.creatorEmail} ${this.title}, 
    ${this.contributor}, ${this.contributorEmail} ${this.contribution}`);
    this.lessonProvider.contributeLesson(this.creator, this.creatorEmail, this.title, this.contributor, this.contribution, this.date)
      .subscribe(res => {
        if (res.message) {
          let toast = this.toastCtrl.create({
            message: "Contribution added!",
            duration: 3000,
            position: 'top'
          });
          toast.present();
          this.navCtrl.popTo(HelloIonicPage);
        }

        if (res.error) {
          let alert = this.alertCtrl.create({
            title: 'Contribution error',
            subTitle: res.error,
            buttons: ['OK']
          });
          alert.present();
        }
      });
    this.contribution = '';
  }
}



