import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, AlertController } from 'ionic-angular';
import { TalliedVote } from '../talliedVoteClass';
import { Storage } from '@ionic/storage';
import { EventProvider } from '../../providers/event/event';



@IonicPage()
@Component({
  selector: 'page-view-event',
  templateUrl: 'view-event.html',
})
export class ViewEventPage {
  userEmail:string;
  votingEvent:any;
  talliedVote = [];
  testValue:any;
  voteList = [];
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private storage: Storage,
    private eventProvider: EventProvider,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController) {
    this.votingEvent = this.navParams.get('vote');
    this.initRanks();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ViewEventPage');
  }

  async getName() {
    await this.storage.get('userEmail').then(value => {
      this.userEmail = value;
    })
  }


  initRanks() {
    this.votingEvent.itemList.forEach(label => {
    this.talliedVote.push({
      label: 0
      });  
    });
  }

  async testOne(event, choice) {
    await this.getName();
    console.log("test: " + this.talliedVote[choice]);
    this.voteList.push({name: choice, rank: this.talliedVote[choice]});
    this.voteList.forEach(vote => {
      console.log(`debug: ${this.userEmail} ${vote.name} ${vote.rank}`);
    });
  }

  vote() {
    let vote:TalliedVote = new TalliedVote();
    
    vote.voter = this.userEmail;
    vote.rankings = this.voteList;
    this.eventProvider.sendVote(this.votingEvent.eventName, this.votingEvent.eventCreator, vote).subscribe(res => {
      if (res.error) {
        let alert = this.alertCtrl.create({
          title: "Voting Error",
          subTitle: res.error,
          buttons: ['OK']
        });
        alert.present();
        this.navCtrl.pop();
      }

      if (res.message) {
        let toast = this.toastCtrl.create({
          message: res.message,
          duration: 3000,
          position: 'top'
        });
        toast.present();
        this.navCtrl.popToRoot();
      }
    });
    console.log("DEBUG: ")
    console.log(this.votingEvent.eventCreator);
    console.log(this.votingEvent.eventName);
    console.log(vote.voter);
    vote.rankings.forEach(obj => {
      console.log(`${obj.name}  ${obj.rank}`);
    });
  }

}



