import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewEventPage } from '../view-event/view-event';
import { Storage } from '@ionic/storage';
import { EventProvider } from '../../providers/event/event';
import { Event } from '../eventClass';


@IonicPage()
@Component({
  selector: 'page-open-events',
  templateUrl: 'open-events.html',
})
export class OpenEventsPage {
  email:string;
  eventsList:Array<Event> = []; 

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private storage:Storage,
    private eventProvider: EventProvider) { } 

  ionViewDidEnter() {
    this.storage.get('userEmail').then(value => {this.email});
    //this.eventProvider.getEmailRevised().then(value => {this.email = value});
    this.eventProvider.viewOpenVotes()
    .subscribe(res => {
      if (res.events.eventCreator !== undefined || res.events.eventCreator !== null ) {
        console.log(res);
        res.events.forEach(ev => {
        const event = new Event(ev.eventCreator, ev.eventName, 
          ev.description, ev.endDate, 
          ev.itemList, ev.guestList, 
          ev.highMediumLowSelected, ev.ordinalSelected);
          this.eventsList.push(event);
          console.log(ev.eventName);
          console.log(event.eventCreator);
        });
        console.log('debug OpenEventsPage');
      }
    });
    
  }

  voteTapped(event, vote) {
    this.navCtrl.push(ViewEventPage, { vote: vote });
  }

}



