import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LessonViewContribPage } from '../lesson-view-contrib/lesson-view-contrib';
import { LessonProvider } from '../../providers/lesson/lesson';


@IonicPage()
@Component({
  selector: 'page-search-lessons',
  templateUrl: 'search-lessons.html',
})
export class SearchLessonsPage {
  lessonToSearch:string;
  
  lessonsResults:Array<any> = [];
  showList:boolean = true;
  showResults:Array<any> = [];
  results:boolean = false;
  noResults:boolean = false;


  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private lessonProvider: LessonProvider) { }

  lessonPicked(event, lesson) {
    this.navCtrl.push(LessonViewContribPage, { lesson: lesson });
  }

  searchLesson() {
    this.lessonProvider.searchLessons(this.lessonToSearch)
      .subscribe(res => { 
        if (res.results.length > 0) {
          this.results = true;
          this.noResults = false;
          this.showResults = res.results;
          this.lessonsResults = res.results;
        } else {
          this.results = false;
          this.noResults = true;
          this.showResults = res.results;
          this.lessonsResults = [{'lesson': "No results found"}];
        }
      });
  }
  onCancel(event) {}
  onClear(event) {}

  ionViewDidLoad() {
    console.log('ionViewDidLoad SearchLessonsPage');
  }

}



