//Angular, ionic, etc... imports
import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
//local App imports for pushing page
import { EventPage } from '../event/event';
import { InputLessonsPage } from '../input-lessons/input-lessons';
import { SendRemindersPage } from '../send-reminders/send-reminders';
import { OpenEventsPage } from '../open-events/open-events';
import { VoteResultsPage } from '../vote-results/vote-results';
import { SearchLessonsPage } from '../search-lessons/search-lessons';
import { EventProvider } from '../../providers/event/event';

@Component({
  selector: 'page-hello-ionic',
  templateUrl: 'hello-ionic.html'
})
export class HelloIonicPage {
  userData:any;
  reminderData:any;
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private eventCtrl: EventProvider,
    private alertCtrl: AlertController,
    private http: HttpClient) {
    this.userData = this.navParams.get('data');
    console.log(this.userData.email);
    this.checkReminders();
  }

  onPageWillEnter() {
  }

  async checkReminders() {
    let message = "";
    this.eventCtrl.getReminders(this.userData.email).subscribe(res => {
        if (res.results) {
          let alert = this.alertCtrl.create({
            title: "You have Reminders",
            subTitle: "Reminder name goes here",
            buttons: ['OK']
          });
          alert.present();
        }
    })
  }

  voteResultsClicked(event) {
    this.navCtrl.push(VoteResultsPage, {data: this.userData });
  }

  openEventsClicked(event) {
    this.navCtrl.push(OpenEventsPage);
  }
  
  createLessonClicked(event) {
    this.navCtrl.push(InputLessonsPage);
  }

  createEventClicked(event) {
    this.navCtrl.push(EventPage);
  }

  sendReminderClicked(event) {
    this.navCtrl.push(SendRemindersPage);
  }

  searchLessonsClicked(event) {
    this.navCtrl.push(SearchLessonsPage);
  }

}



