export class Event {
    eventCreator:any;
    eventName:any;
    description:string;
    endDate:string;
    itemList:Array<string>;
    guestList:Array<string>;
    highMediumLowSelected:boolean;
    ordinalSelected:boolean;
  
    constructor(creator:any, title:any, 
        description:string, endDate:string, 
        choice:Array<string>, guests:Array<string>,
        highMedLow:boolean, ordinal:boolean) {
      this.eventCreator = creator;
      this.eventName = title;
      this.description = description;
      this.endDate = endDate;
      this.itemList = choice;
      this.guestList = guests;
      this.highMediumLowSelected = highMedLow;
      this.ordinalSelected = ordinal;
    }
  };


