import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ContributeLessonPage } from '../contribute-lesson/contribute-lesson';


@IonicPage()
@Component({
  selector: 'page-lesson-view-contrib', 
  templateUrl: 'lesson-view-contrib.html',
})
export class LessonViewContribPage {
  lessonView:any;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.lessonView = this.navParams.get('lesson');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LessonViewContribPage');
  }

  goToContributeLesson() {
    this.navCtrl.push(ContributeLessonPage, {lesson: this.lessonView});
  }

}



