import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { Lesson } from '../lessonClasses';
import { Storage } from '@ionic/storage';
import { LessonProvider } from '../../providers/lesson/lesson';

//input lessons logic

@IonicPage()
@Component({
  selector: 'page-input-lessons',
  templateUrl: 'input-lessons.html',
})
export class InputLessonsPage {
  creator:any;
  email:any;
  title:string;
  description:string;
  lesson:string;
  contributions:Array<any> = [];
  contributors:Array<any> = [];
  isPrivate:boolean = false;
  date:Date = Date.apply(Date.now());
  
  guest:string;

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    private storage: Storage, private lessonProvider: LessonProvider, 
    private alertCtrl: AlertController, private toastCtrl: ToastController) {
    this.storage.get('userName').then(value => {this.creator = value});
    this.storage.get('userEmail').then(value => {this.email = value});
    console.log(this.creator);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InputLessonsPage');
  }

  addContributor() {
    this.contributors.push(this.guest);
    this.contributors.forEach(guest => {console.log(`${guest}`)})
    this.guest = '';
  }

  removeContributor(guest) {
    let i = this.contributors.indexOf(guest);
    if (i != -1) {
      this.contributors.splice(i,1);
    }
    this.contributors.forEach(word => {console.log(word)});
  }

  toggled() {
    console.log(this.isPrivate);
  }


  submit() {
    this.lessonProvider.createLesson(
      this.creator,
      this.email,
      this.title,
      this.description,
      this.lesson,
      this.contributions,
      this.contributors,
      this.isPrivate,
      this.date
    ).subscribe(res => {
      if (res.message) {
        let toast = this.toastCtrl.create({
          message: res.message,
          duration: 3000,
          position: 'top'
        });
        toast.present();
        this.navCtrl.pop();
      }
      
      if (res.error) {
        let alert = this.alertCtrl.create({
          title: 'Error',
          subTitle: res.error,
          buttons: ['OK'] 
        });
        alert.present();
      }
    });
  }
  submitTest() {
    console.log(`${this.creator} ${this.lesson}`);
  }

}



