import { Component, ViewChild } from '@angular/core';

import { Platform, MenuController, Nav } from 'ionic-angular';

import { HelloIonicPage } from '../pages/hello-ionic/hello-ionic';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { EventPage } from '../pages/event/event';
import { ViewEventPage } from '../pages/view-event/view-event';
import { ViewLessonsPage } from '../pages/view-lessons/view-lessons';
import { InputLessonsPage } from '../pages/input-lessons/input-lessons';
import { SendRemindersPage } from '../pages/send-reminders/send-reminders';
import { VoteResultsPage } from '../pages/vote-results/vote-results';
import { LoginPage } from '../pages/login/login';

import { Storage } from '@ionic/storage';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { SearchLessonsPage } from '../pages/search-lessons/search-lessons';
import { SettingsPage } from '../pages/settings/settings';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  // make HelloIonicPage the root (or first) page
  user:string;
  email:string;
  image:URL;

  rootPage = LoginPage;
  pages: Array<{title: string, component: any}>;

  constructor(
    public platform: Platform,
    public menu: MenuController,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    private http:HttpClient,
    private storage: Storage
  ) {
    this.initializeApp();

    // set our app's pages
    this.pages = [
      //{ title: 'Eventure Home', component: HelloIonicPage, },
      { title: 'Create Event', component: EventPage},
      { title: 'Search Lessons', component: SearchLessonsPage },
      { title: 'Create Lesson', component: InputLessonsPage },
      { title: 'Send Reminders', component: SendRemindersPage },
      { title: 'Vote results', component: VoteResultsPage }
    ];

    this.storage.get('userName').then(value => {
      this.user = value;
    });
    this.storage.get('userEmail').then(value => {
      this.email = value;
    });

    //this.http.get("http://jsonplaceholder.typicode.com/users")
    //   .map((response) => response)
    //   .subscribe(data => { 
    //     this.user = data[0].name;
    //     this.department = data[0].address.city;
    //   })
  }

  async initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
    await this.getEmail();
  }
   
  async getEmail(){
      await this.storage.get('userName').then(value => {
        this.user = value;
      });
  }


  openPage(page) {
    this.menu.close();
    this.nav.push(page.component);
  }
  logOut() {
    this.storage.clear();
    this.storage.remove('userEmail');
    this.storage.remove('userName');
    this.nav.setRoot(LoginPage);
  }

  settings() {
    let user = {
      user: this.user,
      email: this.email
    }
    this.nav.push(SettingsPage, {user: user});
  }

}



