import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { EventProvider } from '../../providers/event/event';


class Reminder {
  name:string;
  reminderTo:string;
  reminderFrom:string;

  constructor(name, reminderTo, reminderFrom) {
    this.name = name;
    this.reminderTo = reminderTo;
    this.reminderFrom = reminderFrom;
  }
};


@IonicPage()
@Component({
  selector: 'page-send-reminders',
  templateUrl: 'send-reminders.html',
})
export class SendRemindersPage {
  eventToRemind:any;

  remindersToSend:Array<any> = [];
  showList:boolean = true;
  showResults:Array<any> = [];
  results:boolean = false;
  noResults:boolean = false;

  guestsListEmails = new Set();
  guests = [];

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private eventProvider: EventProvider,
    private alertCtrl: AlertController,
    private toastCtrl: ToastController) { }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SendRemindersPage');
  }

  searchReminders() {
    this.eventProvider.searchReminders(this.eventToRemind)
    .subscribe(res => { 
      if (res.results.length > 0) {
        this.results = true;
        this.noResults = false;
        this.showResults = res.results;
        this.remindersToSend = res.results;
      } else {
        this.results = false;
        this.noResults = true;
        this.showResults = res.results;
        this.remindersToSend = [{'reminders': "No results found"}];
      }
    });
    console.log(this.showResults);
  }

  // test(event, reminder, name) {
  //     let pos = this.guestsListEmails.map(function(e) { return e.reminder; }).indexOf(reminder);
  //     let posTwo = this.guestsListEmails.map(function(e) { return e.name; }).indexOf(name);
  //     if (pos > -1 && posTwo > -1) {
  //       //console.log(pos, posTwo);
  //       this.guestsListEmails.splice(pos,1);
  //       return;
  //     } else {
  //       this.guestsListEmails.push({name:name, reminder: reminder});
  //       //this.guestsListEmails.forEach(e => console.log(`${e.reminder} ${e.name}`));
  //     }
  // }

  testSet(event, name, reminder, creator):void {
      this.guestsListEmails.add({name: name, reminder: reminder, creator: creator});
  }

  testButton() {
    this.guestsListEmails.forEach(e => console.log(`${e.name} ${e.reminder} ${e.creator}`));
    const t = Array.from(this.guestsListEmails);
    this.eventProvider.sendReminders(t)
      .subscribe(res => {
        if (res.message) {
          let toast = this.toastCtrl.create({
            message: res.message,
            duration: 3000,
            position: 'top'
          });
          toast.present();
          this.navCtrl.pop();
        }
        
        if (res.error) {
          let alert = this.alertCtrl.create({
            title: 'Reminders Error!',
            subTitle: res.error,
            buttons: ['OK']
          });
          alert.present();
        }
      });
  }
}



