import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { Chart } from 'chart.js';

/**
 * Generated class for the ResultChartsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

class RankedVotesPickOne {
    names:Array<any> = [];
    votes:Array<number> = [];
}





@IonicPage()
@Component({
  selector: 'page-result-charts',
  templateUrl: 'result-charts.html',
})
export class ResultChartsPage {
  @ViewChild('barCanvas') barCanvas;
  @ViewChild('doughnutCanvas') doughnutCanvas;
  @ViewChild('lineCanvas') lineCanvas;
  barChart: any;
  doughnutChart: any;
  lineChart: any;

  voteList:any;
  voteLabel = ["Lion King", "Wall-e", "Beauty & The Beast"];
  voteData = [6, 11, 9];

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private toastCtrl: ToastController) {
      this. voteList = this.navParams.get('vote');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ResultChartsPage');
    this.barChart = new Chart(this.barCanvas.nativeElement, {

      type: 'bar',
      data: {
          labels: this.voteList.voteLabel, //["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
          datasets: [{
              label: this.voteList.eventName,
              data: this.voteList.voteData, //[12, 19, 3, 5, 2, 3],
              backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)'
              ],
              borderColor: [
                  'rgba(255,99,132,1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(255, 159, 64, 1)'
              ],
              borderWidth: .5
          }]
      },
      options: {
          scales: {
              yAxes: [{
                  ticks: {
                      beginAtZero:true
                  }
              }]
          }
      }

  });

//   this.doughnutChart = new Chart(this.doughnutCanvas.nativeElement, {

//       type: 'doughnut',
//       data: {
//           labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
//           datasets: [{
//               label: '# of Votes',
//               data: [12, 19, 3, 5, 2, 3],
//               backgroundColor: [
//                   'rgba(255, 99, 132, 0.2)',
//                   'rgba(54, 162, 235, 0.2)',
//                   'rgba(255, 206, 86, 0.2)',
//                   'rgba(75, 192, 192, 0.2)',
//                   'rgba(153, 102, 255, 0.2)',
//                   'rgba(255, 159, 64, 0.2)'
//               ],
//               hoverBackgroundColor: [
//                   "#FF6384",
//                   "#36A2EB",
//                   "#FFCE56",
//                   "#FF6384",
//                   "#36A2EB",
//                   "#FFCE56"
//               ]
//           }]
//       }

//   });

 }

 sendResults() {
     let toast = this.toastCtrl.create({
         message:"Results have been sent!",
         duration:3000,
         position:'top'
     });
     toast.present();
     this.navCtrl.popToRoot();
 }

}



