import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HttpClientModule } from '@angular/common/http';
import { ServerProvider } from '../providers/server/server';
import { IonicStorageModule } from '@ionic/storage';
import { Camera } from '@ionic-native/camera/ngx';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

//Pages of app go HERE
import { EventPage } from '../pages/event/event';
import { ViewEventPage } from '../pages/view-event/view-event';
import { InputLessonsPage } from '../pages/input-lessons/input-lessons';
import { ViewLessonsPage } from '../pages/view-lessons/view-lessons';
import { SendRemindersPage } from '../pages/send-reminders/send-reminders';
import { OpenEventsPage } from '../pages/open-events/open-events';
import { VoteResultsPage } from '../pages/vote-results/vote-results';
import { SearchLessonsPage } from '../pages/search-lessons/search-lessons';
import { LessonViewContribPage } from '../pages/lesson-view-contrib/lesson-view-contrib';
import { LoginPage } from '../pages/login/login';
import { SignUpPage } from '../pages/sign-up/sign-up';
import { HelloIonicPage } from '../pages/hello-ionic/hello-ionic';
import { ItemDetailsPage } from '../pages/item-details/item-details';
import { RegisterProvider } from '../providers/register/register';
import { EventProvider } from '../providers/event/event';
import { Event } from '../pages/eventClass';
import { LessonProvider } from '../providers/lesson/lesson';
import { ContributeLessonPage } from '../pages/contribute-lesson/contribute-lesson';
import { ResultChartsPage } from '../pages/result-charts/result-charts'
import { VotesProvider } from '../providers/votes/votes';
import { SettingsPage } from '../pages/settings/settings';


@NgModule({
  declarations: [
    MyApp,
    HelloIonicPage,
    ItemDetailsPage,
    EventPage,
    ViewEventPage,
    InputLessonsPage,
    ViewLessonsPage,
    SendRemindersPage,
    OpenEventsPage,
    VoteResultsPage,
    SearchLessonsPage,
    LoginPage,
    LessonViewContribPage,
    SignUpPage,
    ContributeLessonPage,
    ResultChartsPage,
    SettingsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HelloIonicPage,
    ItemDetailsPage,
    EventPage,
    ViewEventPage,
    InputLessonsPage,
    ViewLessonsPage,
    SendRemindersPage,
    OpenEventsPage,
    VoteResultsPage,
    SearchLessonsPage,
    LoginPage, 
    LessonViewContribPage,
    SignUpPage,
    ContributeLessonPage,
    ResultChartsPage,
    SettingsPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ServerProvider,
    RegisterProvider,
    EventProvider,
    LessonProvider,
    VotesProvider,
    Camera
  ]
})
export class AppModule {}



